.containerPortfolio{
    min-height: 200vh;
    align-items: center;
    justify-items: center;
    align-content: center;
    justify-content: center;
}
.titlePortfolio{
    font-weight: 600;
    font-size: 45px;
}
.descrptionPortfolio{
    font-family:"Open Sans", sans-serif;
    font-size:18px;
    width: 30%;
    text-align: center;
}
.itemPortfolio{
    width: 25%;
    padding: 40px 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.titleItemPortfolio{
    text-align: center;
}
.textItemPortfolio{
    margin-bottom: 30px;
}
.flexPortfolio{
    width: 100%;
    flex-wrap: wrap;
}
@media screen and (max-width:1000px){
    .descrptionPortfolio{
        width: 75%;
        font-size: 1.2rem;
    }
    .titlePortfolio{
        font-weight: 400;
        font-size: 1.8rem;
    }
    .itemPortfolio{
        width: 100%;
        padding: 30px 0px;
    }
    .flexPortfolio{
        width: 90%;
    }
}